:root {
    --offset: 25px;
    --titleWrapperWidth: 130px;
    --tabColor: #002638;

    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body, html, #root {
    width: 100%;
    height: 100%;
    margin: 0 0;
    overflow: hidden;
}

.DataBinApp {
    background-color: #c7edff;
    width: calc(100% - var(--offset));
    height: calc(100% - var(--offset));
    margin-left: calc(var(--offset) / 2);
    margin-top: calc(var(--offset) / 2);
    outline: 3.5px solid #005e8d;
}

.DataBinStructure {
    width: 100%;
    height: calc(100% - 110px);
}

.DataBinStructureContent {
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    margin-left: 5px;
    margin-top: 5px;
    overflow-y: auto;
}

html[dir='rtl'] .DataBinStructureContent {
    margin-left: 0;
    margin-top: 0;
    margin-right: 5px;
    margin-bottom: 5px;
}

.DataBinStructureTag {
    margin-bottom: -1px;
}

.DataBinStructureTagName {
    position: relative;
    top: -10px;
    font-size: 20px;
}

.DataBinRibbon {
    background-color: #0074ad;
    height: 50px;
}

.DataBinTitleWrapper {
    height: 100%;
    width: var(--titleWrapperWidth);
    text-align: center;
    vertical-align: middle;
}

.DataBinTitle {
    font-size: 32px;
    color: white;
    text-align: center;
}

.DataBinTabs {
    width: calc(100% - var(--titleWrapperWidth));
    position: absolute;
    top: calc(var(--offset) - 7.5px);
    left: calc(var(--titleWrapperWidth) + 20px);
}

.DataBinTabsWrapper {
    color: white;
}

.DataBinTab {
    position: inherit;
    width: 80px;
}

.DataBinTab * {
    background-color: var(--tabColor) !important;
    border: none !important;
    color: white !important;
}

.DataBinTabFile {
    position: absolute;
    left: 0;
    width: 150px;
}

.DataBinTabLang {
    position: absolute;
    left: calc(100% - 190px);
    bottom: 37.5px;
    width: 150px;
}

.DataBinRibbon button {
    width: 35px;
    height: 35px;
    position: absolute;
    right: 200px;
    top: 1.5px;
    background-color: #002638;
    border: none;
    color: white;
    font-size: 20px;
    font-weight: 500;
    font-family: inherit;
}

.DataBinRibbon button:hover {
    font-size: 25px;
}

html[dir='rtl'] .DataBinTabLang {
    right: calc(100% - 35px)
}

html[dir='rtl'] .DataBinTabFile {
    right: 160px;
}

html[dir='rtl'] .DataBinRibbon button {
    right: calc(100% - 80px);
}

.DataBinToolbox {
    background-color: #77d4ff;
    height: 80px;
    overflow-x: scroll;
}

.DataBinToolboxCategories {
    height: 20px;
    background-color: #c7edff;
    --categories: 3;
}

.DataBinToolboxCategory {
    height: 100%;
    width: calc(100% / var(--categories));    
    color: black;
    font-size: 12.5px;
    margin: 0;
    padding: 0;
    position: relative;
    top: -2.5px;
    border: none;
    font-family: inherit;
}

.DataBinToolboxCategory.selectedType {
    font-weight: 700;
    font-size: 13px;
}

.DataBinToolbox button:not(.DataBinToolboxCategory) {
    width: 35px;
    height: 35px;
    margin-left: 5px;
    margin-right: 5px;
    padding-left: 2px;
    padding-top: -5px;
    background-color: transparent;
    border: 0;
}

.DataBinToolbox button:not(.DataBinToolboxCategory).selectedType {
    background-color: rgb(0, 86, 156);
}

.DataBinStructureIndented {
    margin-top: 5px;
    margin-left: 30px;
    margin-bottom: -37.5px;
}

.DataBinStructureTagIcon {
    width: 37px;
    height: 37px;
    padding: 0;
    border: 0;
    outline: 0;
    background-color: transparent;
}

.DataBinStructureTagIcon:hover {
    background-color: #77d4ff;
}

.DataBinDialogBG {
    z-index: 50;
    background-color: rgba(53, 106, 253, 0.2);
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;
}

.DataBinDialog {
    background-color: #c7edff;
    border: 5px solid black;
    width: 1000px;
    height: 250px;
    top: calc(50% - 120px);
    left: calc(50% - 500px);
    position: absolute;
}

.DataBinDialogText {
    width: 650px;
    height: 150px;
    line-height: 150px;
    margin-top: 25px;
    margin-left: calc(50% - 310px);
    font-size: 24px;
    text-align: center;
}

.DataBinDialogText span {
    display: inline;
    vertical-align: middle;
    line-height: normal;
}

.DataBinDialogButtons {
    width: 100%;
    text-align: center;
    margin-top: 15px;
}

.DataBinDialogButtons button {
    height: 40px;
    font-size: 20px;
    margin-left: 25px;
    margin-right: 25px;
    border: 2px solid rgb(255, 255, 255);
    color: rgb(255, 255, 255);
    background-color: #005f8b;
}

.DataBinDialogButtons button:hover {
    background-color: #00344d;
}

.DataBinCrash {
    width: 100%;
    height: 100%;
    background-color: #0074ad;
    color: white;
}

.DataBinCrash * {
    margin: 0 0;
}

.DataBinCrashTitle {
    width: 100%;
    height: 50px;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 40px;
    font-size: 30px;
}

.DataBinCrashBody {
    width: 100%;
    height: calc(100% - 50px);
    text-align: center;
    font-size: 20px;
}

.DataBinCrashReload {
    background-color: #002638;
    border: 2px solid white;
    width: 240px;
    height: 40px;
    font-size: 15px;
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.DataBinCrashReload:hover {
    background-color: white;
    border: 2px solid #002638;
    color: #002638;
}

.DataBinCrashDetails {
    background-color: #002638;
    border: 2px solid white;
    resize: none;
    width: 80%;
    height: 30%;
    font-size: 20px;
    color: white;
}

.DataBinStructureButton {
    width: 20px;
    height: 20px;
    background-color: #005e8d;
    border: none;
    color: white;
    font-weight: 600;
    font-size: 15px;
}

.DataBinStructureButton div {
    position: relative;
    left: -3.5px;
    top: -.5px;
}

html[dir='rtl'] .DataBinStructureButton div {
    right: -3.5px;
}

.DataBinHelpWrapper {
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(53, 106, 253, 0.2);
}

.DataBinHelp {
    width: calc(100vw - 130px);
    height: calc(100vh - 130px);
    line-height: 150px;
    font-size: 24px;
    text-align: center;
    background-color: #4fb7eb;
    top: 65px;
    left: 65px;
    position: absolute;
}

.DataBinHelpRibbon {
    width: 100%;
    top: 0;
    position: absolute;
    height: 50px;
    background-color: #005e8d;
    font-weight: bold;
    font-size: 27.5px;
    color: white;
    text-align: center;
    line-height: 50px;
}

.DataBinHelpContent {
    height: calc(100% - 50px);
    background-color: #ffffff22;
    width: 100%;
    position: absolute;
    left: 0;
    top: 50px;
}

.DataBinHelpRibbon>button {
    right: 5px;
    top: 5px;
    position: absolute;
    border: none;
    background-color: #001d2c;
    color: white;
    font-weight: bold;
    font-size: 22.5px;
    width: 30px;
    height: 30px;
}

.DataBinHelpContented {
    width: calc(100% - 200px);
    height: 100%;
    left: 200px;
    position: absolute;
    overflow-y: auto;
}

.DataBinHelpStructure {
    width: 200px;
    height: 100%;
    left: 0;
    position: absolute;
    background-color: #002638;
    text-align: left;
    line-height: 0px;
    color: white;
    overflow-x: auto;
}

.help_name {
    height: 25px;
    margin: 0;
    border: none;
    color: white;
    font-family: inherit;
    font-size: 18px;
    background-color: #00000000;
    cursor: pointer;
}

div.help_indent {
    margin-left: 20px;
}

div.help_s_text {
    position: relative;
    top: -5px;
    display: inline;
    word-wrap:break-word;
}

button.help_switcher {
    width: 25px;
    height: 25px;
    border: none;
    padding: 0;
    background-color: #00000000;
    cursor: pointer;
}

button.linked {
    cursor: pointer;
    background: none;
    border: none;
    font-size: 23.5px;
    font-family: inherit;
    color: rgb(0, 86, 156);
}

.StructureMore {
    width: calc(200px + 15px)
}

.StructureLess {
    width: calc(100% - 200px - 15px);
    left: 215px;
}